<template>
  <div class="logFund">
    <el-dialog 
      :title="dialog.title" 
      :visible.sync="dialog.show"
      :close-on-click-modal='false'
      :close-on-press-escape='false'
      :modal-append-to-body="false">
      <div class="form">
        <el-form 
          ref="form" 
          :model="form"
          :rules="form_rules"
          label-width="120px" 
          style="margin:10px;width:auto;"
        >
          <el-form-item prop='name' label="游戏名字:">
            <el-input type="name" v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item prop='url1' label="完整版地址:">
            <el-input type="url1" v-model="form.url1"></el-input>
          </el-form-item>

          <el-form-item prop='password1'  label="完整版密码:">
            <el-input type="password1" v-model="form.password1"></el-input>
          </el-form-item>

          <el-form-item prop='url2' label="独立版地址">
            <el-input type="url2" v-model="form.url2"></el-input>
          </el-form-item>

          <el-form-item prop='password2' label="独立版密码">
            <el-input type="password2" v-model="form.password2"></el-input>
          </el-form-item>

          <el-form-item  class="text_right">
              <el-button @click="dialog.show = false">取 消</el-button>
              <el-button type="primary" @click='onSubmit("form")'>提  交</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {updown} from '@/api/userHT'
export default {
  name: "downfound",
  props: {
    dialog: Object,
    form: Object
  },
  data() {
    return {
      form_rules: {
        name: [
          { required: true, message: "游戏名字不能为空！", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    onSubmit(form) {
      this.$refs[form].validate(valid => {
        if (valid) {
          // 表单数据验证完成之后，提交数据;
          console.log(this.form)
          updown(this.form.id,this.form)
            .then(res => {
              if (res) {
                this.$message({
                  message: "保存成功！", 
                  type: "success"
                });
              }
              this.dialog.show = false;
              this.$emit("update");
            });
          }
      });
    }
  }
};
</script>

